<script>
  import { onMount } from "svelte";
  import { Doughnut, Pie } from "svelte-chartjs";
  import { user } from "../stores/User";
  import Navbar from "../components/Navbar.svelte";
  import {
    Chart as ChartJS,
    Title,
    Tooltip,
    Legend,
    ArcElement,
    CategoryScale,
  } from "chart.js";

  ChartJS.register(Title, Tooltip, Legend, ArcElement, CategoryScale);

  let promise = [];

  const getResultados = async () => {
    const options = {
      method: "GET",
      headers: {
        Authorization: $user.token,
      },
    };

    const res = await fetch(
      "http://127.0.0.1:5000/encuesta/resultados/1",
      options
    );
    const data = await res.json();
    const {
      calificacionfinal,
      calificacionseccion1,
      calificacionseccion2,
      calificacionseccion3,
      resultadofinal,
      resultadoseccion1,
      resultadoseccion2,
      resultadoseccion3,
    } = data;

    console.log(data);
    if (res.ok) {
      return [
        {
          labels: ["Si", "No"],
          datasets: [
            {
              label:
                "ENCUESTA PARA LA ORGANIZACION DEL ESTUDIO (" +
                resultadoseccion1 +
                ")",
              data: [calificacionseccion1, 20 - calificacionseccion1],
              backgroundColor: ["#46BFBD", "#F7464A"],
              hoverBackgroundColor: ["#5AD3D1", "#FF5A5E"],
            },
          ],
        },
        {
          labels: ["Si", "No"],
          datasets: [
            {
              label: "ENCUESTA TECNICA DE ESTUDIO (" + resultadoseccion2 + ")",
              data: [calificacionseccion2, 20 - calificacionseccion2],
              backgroundColor: ["#46BFBD", "#F7464A"],
              hoverBackgroundColor: ["#5AD3D1", "#FF5A5E"],
            },
          ],
        },
        {
          labels: ["Si", "No"],
          datasets: [
            {
              label:
                "ENCUESTA PARA MOTIVACION SOBRE MOTIVACION PARA EL ESTUDIO (" +
                resultadoseccion3 +
                ")",
              data: [calificacionseccion3, 20 - calificacionseccion3],
              backgroundColor: ["#46BFBD", "#F7464A"],
              hoverBackgroundColor: ["#5AD3D1", "#FF5A5E"],
            },
          ],
        },
        {
          labels: ["Si", "No"],
          datasets: [
            {
              label: "Total (" + resultadofinal + ")",
              data: [calificacionfinal, 60 - calificacionfinal],
              backgroundColor: ["#46BFBD", "#F7464A"],
              hoverBackgroundColor: ["#5AD3D1", "#FF5A5E"],
            },
          ],
        },
      ];
    } else {
      throw new Error(data);
    }
  };

  onMount(() => {
    promise = getResultados();
  });
</script>

<svelte:head>
  <link rel="stylesheet" type="text/css" href="./css/style_form.css" />
  <link rel="stylesheet" type="text/css" href="./css/bootstrap.min.css" />
</svelte:head>
<div class="">
  <!-- main -->
  <div class="main">
    <div class="topbar">
      <div class="toggle">
        <ion-icon name="menu-outline" />
      </div>

      <div class="user">
        <img src="./imgs/login-icon.png" alt="" />
      </div>
    </div>
    <!-- cards -->
    <div class="panel panel-default">
      {#await promise}
        <p>...waiting</p>
      {:then graficas}
        <div class="cardBox">
          {#each graficas as grafica}
            <div class="card">
              <h4>{grafica.datasets[0].label}</h4>
              <Pie data={grafica} />
            </div>
          {/each}
        </div>
      {:catch error}
        <p class="text-center text-danger">{error}</p>
      {/await}
    </div>
    <div class="d-flex justify-content-center">
      <button
        style="width:15%"
        class=" centrar btn btn-outline-primary"
        type="submit"
        value="submit"
        on:click={() => (location.href = "/encuesta1")}
      >
        Realizar Encuesta
      </button>
    </div>
  </div>
  <Navbar />
</div>
